import React from "react";
import { Col, Row, ProgressBar, Container } from "react-bootstrap";

function Techstack({ category, name, initialRating }) {
  const percentage = (initialRating / 5) * 100; // Calculate the percentage

  return (
    <Container>
      <Row style={{ justifyContent: "left", paddingBottom: "10px" }}>
        <Col className="skill-name" xs={4}>
          <strong>{category}</strong> - {name}
        </Col>
        <Col xs={6}>
          <ProgressBar 
            now={percentage} 
            label={`(${percentage}%)`} 
            variant="info" 
            style={{ height: '20px' }} 
          />
        </Col>
      </Row>
    </Container>
  );
}

export default Techstack;
